#clock {
  font-family: Roboto;
  height: 100vh;
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  color: #fff;
  background-color: #111;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}
#clock h1 {
  font-size: 7em;
  cursor: pointer;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}
#clock h1::-moz-selection {
  background-color: transparent;
}
#clock h1::selection {
  background-color: transparent;
}
.faded {
  opacity: 0;
}
#instruction {
  position: absolute;
  width: 100vw;
  text-align: center;
  color: #fff;
  font-family: Roboto;
  font-size: 18px;
}



